.search-page {
    height: 100vh;
    width: 100vw;
    background-color: #7289DA;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title {
    margin: 30px 0;
    color: #FFFFFF;
}

.input-container {
    display: flex;
    width: 30vw;
    height: 60px;
}

input {
    border: none;
    box-sizing: border-box;
}

input:focus {
    outline: none !important;
    box-shadow: none !important;
}

.input {
    flex: 1;
    border-radius: 4px;
    color: #000000;
    font-size: 16px;
    padding: 13px;
}

.auto-fill-hint {
    position: absolute;
    height: 60px;
    margin-top: 20px;
    padding-left: 13px; /* same as input padding left */
    color: gray;
}

.submit-button {
    background-color: #F47B68;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: bold;
    border-radius: 4px;
    padding: 8px 20px;
    margin: 30px 0;
    cursor: pointer;
}

@media only screen and (max-width: 1260px) {
    .input-container {
        width: 90vw;
    }
}
